@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: "Righteous", cursive;
}

/* Utilities */

a {
  text-decoration: none;
}
p {
  font-family: "Poppins", sans-serif;
}
input::-webkit-input-placeholder {
  font-family: "Poppins", sans-serif;
}
input:-ms-input-placeholder {
  font-family: "Poppins", sans-serif;
}
input,
input::placeholder {
  font-family: "Poppins", sans-serif;
}
.containers:not(nav) {
  max-width: 1300px;
  padding: 60px 30px;
  margin: auto;
}
.tap {
  position: relative;
  overflow: hidden;
  display: inline-block;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-user-drag: none;
  transition: -webkit-transform 0.2s ease;
  transition: transform 0.2s ease;
  transition: transform 0.2s ease, -webkit-transform 0.2s ease;
  -webkit-transition: transform 0.2s ease;
  -moz-transition: transform 0.2s ease;
  -ms-transition: transform 0.2s ease;
  -o-transition: transform 0.2s ease;
}
.tap:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}
.tap span {
  position: absolute;
  display: inline-block;
  width: 1px;
  height: 1px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background: rgba(255, 255, 255, 0.7);
  animation: buble 0.5s linear both;
  -webkit-animation: buble 0.5s linear both;
  pointer-events: none;
}
@-webkit-keyframes buble {
  0% {
    transform: scale(1);
    opacity: 1;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(100);
            transform: scale(100);
    opacity: 0;
    zoom: 5;
    -webkit-transform: scale(200);
    -moz-transform: scale(100);
    -ms-transform: scale(100);
    -o-transform: scale(100);
  }
}
@keyframes buble {
  0% {
    transform: scale(1);
    opacity: 1;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
  }
  100% {
    -webkit-transform: scale(100);
            transform: scale(100);
    opacity: 0;
    zoom: 5;
    -webkit-transform: scale(200);
    -moz-transform: scale(100);
    -ms-transform: scale(100);
    -o-transform: scale(100);
  }
}
.containers:not(#writter .containers) > h1 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-bottom: 35px;
  padding: 10px 25px;
  background: linear-gradient(to bottom, #ff6f1e, #de2800);
  color: #fff;
}

/* Header from here */
header {
  background: linear-gradient(to bottom, #c1ffed, #8ceaff);
}

header nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
}
.logo,
.logo-2 {
  width: 160px;
}

header nav ul li {
  margin: 0 5px;
}
header nav ul li a {
  padding: 7px 12px;
  color: rgb(70, 70, 70);
  font-size: 20px;
  position: relative;
}
header nav ul li a:hover {
  color: rgb(255, 255, 255);
  background: linear-gradient(to bottom, rgb(0 245 189), rgb(0 148 148));
  text-shadow: 1px 1px 3px #0009;
}
header a.active {
  color: rgb(255, 255, 255);
  background: linear-gradient(to bottom, rgb(0 245 189), rgb(0 148 148));
  text-shadow: 1px 1px 3px #0009;
}
.nav_left > a {
  color: #fff;
  background: linear-gradient(to bottom, #ff4572, #a30027);
  padding: 7px 12px;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  margin-left: 10px;
}
.nav_left {
  display: flex;
}
img[alt="menu"] {
  display: none;
  cursor: pointer;
}
.nav_search input {
  border: none;
  background: transparent;
}
.nav_search {
  background: #fff;
  border-radius: 30px;
  padding: 3px 13px;
  border: 3px solid #01a1a1;
}
.nav_search img {
  width: 20px;
}

/* hero section from here */

#hero {
  min-height: 550px;
  position: relative;
  background: url(/static/media/bg.07994231.jpg) center center no-repeat;
  background-size: cover;
  color: #fff;
}
#hero::after {
  content: "";
  background: linear-gradient(to right, #94001b8f, #0038a88a);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
#hero .cap {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  left: 50%;
  width: 60%;
  z-index: 2;
  max-width: 1000px;
}
#hero .cap .btns {
  margin-top: 30px;
}
#hero .cap .btns a {
  color: #fff;
  padding: 7px 12px;
  border-radius: 12px;
  -webkit-border-radius: 12px;
  -moz-border-radius: 12px;
  -ms-border-radius: 12px;
  -o-border-radius: 12px;
  font-size: 20px;
  border: 2px solid rgb(255, 255, 255);
  text-shadow: 1px 1px 5px #000;
}
#hero .cap .btns a:first-child {
  background: linear-gradient(to bottom, #ff9b26, #ff4d00);
}
#hero .cap .btns a:nth-child(2) {
  background: linear-gradient(to bottom, #3aff4e, #0fbc00);
  margin: 0 10px;
}
#hero .cap .btns a:nth-child(3) {
  background: linear-gradient(to bottom, #de59ff, #db00ff);
}
#hero .cap h1 {
  text-shadow: 1px 1px 7px #000;
}

/* recent blog from here */
#recent_blog .d-grid {
  grid-template-columns: 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}
.blog_middle {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  min-height: 180px;
}
.blog {
  padding: 20px;
  background: #dcdcdc;
  border-radius: 10px;
  box-shadow: 1px 2px 4px #00000069;
}
.blog h3 {
  margin-bottom: 20px;
  text-align: center;
  padding: 10px 20px;
  background: linear-gradient(to bottom, #336cff, #0330d2);
  color: #fff;
}
.blog_bottom {
  margin-top: 20px;
}
.like_count {
  font-size: 20px;
}
.blog_bottom .d-flex {
  background: #b9b9b9;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 7px 12px;
  border-radius: 30px;
}
.blog img[alt="like"] {
  cursor: pointer;
}
.blog_des {
  margin-bottom: 0;
}
#recent_blog .containers > a {
  padding: 7px 12px;
  font-size: 18px;
  border: 2px solid #4646ff;
  border-radius: 25px;
  margin-top: 20px;
}

/* about from here */

#about .containers > div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

/* writter from here */

#writter {
  background: linear-gradient(to right bottom, #ff5029, #9f0000);
  color: #fff;
}
#writter .containers > div {
  width: 50%;
  margin: auto;
  text-align: center;
}
#writter .containers > div a {
  color: #fff;
  background: linear-gradient(to bottom, #ff8616, #d94100);
  padding: 7px 12px;
  border-radius: 11px;
  border: 2px solid #fff;
  text-shadow: 1px 1px 4px #0000009e;
}

/* member section here */

#member {
  color: #fff;
  padding: 60px 30px;
  background: url(/static/media/member.32dcb285.jpg) center center no-repeat;
  background-size: cover;
  position: relative;
}
#member::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right bottom, #29a6af86, #0016a99c);
  z-index: 1;
}
#member > div a {
  color: #fff;
  background: linear-gradient(to bottom, #ff6ba0, #ba0021);
  padding: 7px 12px;
  border-radius: 11px;
  box-shadow: 0px 0px 7px -1px #000;
  border: 2px solid #fff;
  text-shadow: 1px 1px 4px #0000009e;
}
#member > div {
  width: 60%;
  margin: auto;
  padding: 20px;
  text-align: center;
  border: 3px solid #fff;
  z-index: 2;
  position: relative;
}
.like_count {
  cursor: default;
}
#member > div h1 {
  text-shadow: 1px 1px 10px #000;
}

/* footer menu from here */

#footer_menu {
  background: #1a1a1a;
  color: #fff;
}
#footer_menu .containers {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  gap: 30px;
}
.footer_menu_item:first-child a {
  padding: 7px 12px;
  color: #fff;
  text-shadow: 1px 1px 5px #000;
}
.footer_menu_item:first-child a:first-child {
  background: linear-gradient(to bottom, #ff9b26, #ff4d00);
}
.footer_menu_item:first-child a:last-child {
  background: linear-gradient(to bottom, #de59ff, #db00ff);
}
#footer_menu .media {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
  width: 60%;
  grid-gap: 10px 20px;
  gap: 10px 20px;
  margin: auto;
}
#footer_menu .media a {
  height: 45px;
  width: 45px;
  margin: auto;
  border: 2px solid #fff;
  padding: 5px;
  box-sizing: content-box;
  border-radius: 50%;
  transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
}
#footer_menu .media a:hover {
  background: #6d000f;
}
#footer_menu .media img {
  margin: auto;
  display: block;
  width: 30px;
}
#footer_menu .app {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 20px;
  background: #474747;
}
#footer_menu .app img {
  width: 130px;
  margin-top: 5px;
}
.footer_send h5 {
  background: #474747;
  border: 2px solid #fff;
  padding: 10px;
}
.footer_send button img {
  width: 25px;
}
.footer_send button {
  border: none;
  background: #b0002a;
  padding: 7px 12px;
}
.footer_send input {
  background: #520014;
  border: none;
  color: #fff;
}
.footer_send input::-webkit-input-placeholder {
  color: #a8a8a8;
}
.footer_send input:-ms-input-placeholder {
  color: #a8a8a8;
}
.footer_send input::placeholder {
  color: #a8a8a8;
}
.footer_send .d-flex {
  border: 2px solid #fff;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
footer {
  font-family: "Poppins", sans-serif;
  font-size: 13px;
  background: #77545b;
  color: #fff;
  display: flex;
  justify-content: center;
  padding: 14px;
}
#res_nav {
  display: none;
}
.blog_photo {
  background: url(/static/media/blog-1.cac009a3.jpg) center center no-repeat;
  background-size: cover;
}

@media screen and (max-width: 1200px) {
  #hero .cap {
    width: 70%;
  }
}
@media screen and (max-width: 1100px) {
  .nav_left {
    display: none;
  }
  #writter .containers > div {
    width: 70%;
  }
  img[alt="menu"] {
    display: block;
  }
  #res_nav {
    position: fixed;
    z-index: 10;
    width: 100%;
    background: #b90011a6;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 1;
  }
  #res_nav input {
    font-size: 20px;
  }
  #res_nav li a {
    font-size: 30px;
    color: #fff;
    background: #ff01b1b8;
    padding: 5px 10px;
    margin-bottom: 10px;
    border: 2px solid #fff;
    box-shadow: 0px 0px 4px 0px #000000c2;
  }
  #res_nav > img {
    cursor: pointer;
    background: #fff;
    padding: 15px;
    position: absolute;
    border-radius: 50%;
    right: 40px;
    top: 40px;
  }
  #footer_menu .media {
    width: 100%;
  }
}
@media screen and (max-width: 900px) {
  #member > div {
    width: 80%;
  }
  #hero .cap {
    width: 80%;
  }
  #recent_blog .d-grid {
    grid-template-columns: 1fr;
  }
  #footer_menu .containers {
    grid-template-columns: 1fr 1fr;
  }
  #about .containers > div {
    grid-template-columns: 1fr;
    grid-gap: 30px;
    gap: 30px;
  }
}
@media screen and (max-width: 700px) {
  #writter .containers > div {
    width: 85%;
  }
}
@media screen and (max-width: 600px) {
  #footer_menu .containers {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 500px) {
  #writter .containers > div {
    width: 100%;
  }
  .blog_middle {
    display: grid;
    grid-template-columns: 1fr;
  }
  .blog_middle {
    min-height: auto;
  }
  .blog_photo {
    min-height: 180px;
  }
  #member > div {
    width: 100%;
  }
  .logo,
  .logo-2 {
    width: 120px;
  }
  img[alt="menu"] {
    width: 35px;
  }
}

